   margin: 10px 0;
  height: 50px;
  line-height: 50px;
  text-align: center;
}
.form .form-btn .btn {
  padding: 5px 10px;
  margin: 0 10px;
  cursor: pointer;
  color: #fff;
  border-radius: 5px;
}
