}
.model {
  width: 250px;
  height: 100px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background: #f9f9f9;
  text-align: center;
  line-height: 100px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  color: #666;
  z-index: 9999;
}
.showModel {
  display: block;
  opacity: 1;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
